import { useCallback } from 'react'
import { createClient } from '@/lib/supabase/client'
import { toast } from 'sonner'
import { logger } from '@/lib/logger'
import { markItemAsReceived, markItemAsReceiptCanceled, markItemAsStatementReceived, markItemAsStatementCanceled } from '@/stores/purchaseMemoryStore'

export interface ConfirmDateActionConfig {
  field: 'statement_received' | 'actual_received'
  confirmMessage: {
    confirm: string
    cancel: string
  }
  successMessage: {
    confirm: string
    cancel: string
  }
  completedText: string
  waitingText: string
}

export interface UseConfirmDateActionProps {
  config: ConfirmDateActionConfig
  currentUserName: string | null
  canPerformAction: boolean
  purchaseId?: number | string  //     purchase ID
  onUpdate?: () => void
  onOptimisticUpdate?: (params: {
    itemId: number
    selectedDate?: Date
    action: 'confirm' | 'cancel'
    receivedQuantity?: number
    itemInfo?: {
      item_name?: string
      specification?: string
      quantity?: number
      unit_price_value?: number
      amount_value?: number
      remark?: string
      received_quantity?: number
    }
  }) => void
}

export function useConfirmDateAction({
  config,
  currentUserName,
  canPerformAction,
  purchaseId,
  onUpdate,
  onOptimisticUpdate
}: UseConfirmDateActionProps) {
  const supabase = createClient()

  const handleConfirm = useCallback(async (
    itemId: number | string,
    selectedDate: Date,
    itemInfo?: {
      item_name?: string
      specification?: string
      quantity?: number
      unit_price_value?: number
      amount_value?: number
      remark?: string
      received_quantity?: number
    },
    receivedQuantity?: number
  ) => {
    
    if (!canPerformAction) {
      logger.warn(`  `, { canPerformAction, currentUserName })
      toast.error(`${config.field === 'statement_received' ? '' : ''}   .`)
      return
    }

    const itemIdStr = String(itemId)
    const numericId = typeof itemId === 'number' ? itemId : Number(itemId)

    if (Number.isNaN(numericId)) {
      logger.error('  ID', { itemId, numericId })
      toast.error('   ID .')
      return
    }

    //   
    if (itemInfo) {
      const confirmMessage = `: ${itemInfo.item_name || '-'}
: ${itemInfo.specification || '-'}
: ${itemInfo.quantity?.toLocaleString() || 0}
: ${itemInfo.unit_price_value?.toLocaleString() || 0}
: ${itemInfo.amount_value?.toLocaleString() || 0}
: ${itemInfo.remark || '-'}

${config.confirmMessage.confirm}`
      
      if (!window.confirm(confirmMessage)) {
        return
      }
    }

    try {
      let updateData: any

      if (config.field === 'statement_received') {
        updateData = {
          is_statement_received: true,
          statement_received_date: selectedDate.toISOString(),
          statement_received_by_name: currentUserName
        }
      } else if (config.field === 'actual_received') {
        updateData = {
          actual_received_date: selectedDate.toISOString(),
          is_received: true,
          received_quantity: receivedQuantity !== undefined ? receivedQuantity : (itemInfo?.received_quantity !== undefined ? itemInfo.received_quantity : null)
        }
      }

      logger.debug('  :', updateData)

      const { data, error } = await supabase
        .from('purchase_request_items')
        .update(updateData)
        .eq('id', numericId)
        .select()

      logger.debug(' DB  :', { data, error })

      if (error) {
        logger.error(' DB  ', error)
        throw error
      }

      logger.info(' DB  ', data)

      //     
      if (purchaseId) {
        if (config.field === 'actual_received') {
          const memoryUpdated = markItemAsReceived(purchaseId, numericId, selectedDate.toISOString(), receivedQuantity)
          if (!memoryUpdated) {
            logger.warn('[useConfirmDateAction]     ', { 
              purchaseId, 
              itemId: numericId 
            })
          }
        } else if (config.field === 'statement_received') {
          const memoryUpdated = markItemAsStatementReceived(purchaseId, numericId, selectedDate.toISOString(), currentUserName || undefined)
          if (!memoryUpdated) {
            logger.warn('[useConfirmDateAction]      ', { 
              purchaseId, 
              itemId: numericId 
            })
          }
        }
      }

      if (onOptimisticUpdate) {
        onOptimisticUpdate({
          itemId: numericId,
          selectedDate,
          action: 'confirm',
          receivedQuantity: receivedQuantity !== undefined ? receivedQuantity : itemInfo?.received_quantity,
          itemInfo
        })
      }

      //    onUpdate 
      if (onUpdate) {
        onUpdate()
      }
      
      toast.success(config.successMessage.confirm)
    } catch (error) {
      logger.error('   ', error)
      toast.error(`${config.field === 'statement_received' ? '' : ''}     .`)
    }
  }, [config, currentUserName, canPerformAction, purchaseId, onUpdate, onOptimisticUpdate, supabase])

  const handleCancel = useCallback(async (
    itemId: number | string,
    itemInfo?: {
      item_name?: string
      specification?: string
      quantity?: number
      unit_price_value?: number
      amount_value?: number
      remark?: string
    }
  ) => {
    if (!canPerformAction) {
      logger.warn(`   `, { canPerformAction, currentUserName })
      toast.error(`${config.field === 'statement_received' ? '' : ''}   .`)
      return
    }

    const itemIdStr = String(itemId)
    const numericId = typeof itemId === 'number' ? itemId : Number(itemId)

    if (Number.isNaN(numericId)) {
      toast.error('   ID .')
      return
    }

    //   
    if (itemInfo) {
      const confirmMessage = `: ${itemInfo.item_name || '-'}
: ${itemInfo.specification || '-'}
: ${itemInfo.quantity?.toLocaleString() || 0}
: ${itemInfo.unit_price_value?.toLocaleString() || 0}
: ${itemInfo.amount_value?.toLocaleString() || 0}
: ${itemInfo.remark || '-'}

${config.confirmMessage.cancel}`
      
      if (!window.confirm(confirmMessage)) {
        return
      }
    }

    try {
      logger.debug(` ${config.field}   `, { 
        itemId, 
        itemName: itemInfo?.item_name 
      })

      let updateData: any

      if (config.field === 'statement_received') {
        updateData = {
          is_statement_received: false,
          statement_received_date: null,
          statement_received_by_name: null
        }
      } else if (config.field === 'actual_received') {
        updateData = {
          actual_received_date: null,
          is_received: false
        }
      }

      logger.debug('   :', updateData)

      const { data, error } = await supabase
        .from('purchase_request_items')
        .update(updateData)
        .eq('id', numericId)
        .select()

      logger.debug('  DB  :', { data, error })

      if (error) {
        logger.error(' DB  ', error)
        throw error
      }

      logger.info(` ${config.field}   `, data)

      //     
      if (purchaseId) {
        if (config.field === 'actual_received') {
          const memoryUpdated = markItemAsReceiptCanceled(purchaseId, numericId)
          if (!memoryUpdated) {
            logger.warn('[useConfirmDateAction]     ', { 
              purchaseId, 
              itemId: numericId 
            })
          }
        } else if (config.field === 'statement_received') {
          const memoryUpdated = markItemAsStatementCanceled(purchaseId, numericId)
          if (!memoryUpdated) {
            logger.warn('[useConfirmDateAction]      ', { 
              purchaseId, 
              itemId: numericId 
            })
          }
        }
      }

      if (onOptimisticUpdate) {
        onOptimisticUpdate({
          itemId: numericId,
          action: 'cancel',
          itemInfo
        })
      }

      //    onUpdate 
      if (onUpdate) {
        onUpdate()
      }
      
      toast.success(config.successMessage.cancel)
    } catch (error) {
      logger.error(` ${config.field}   `, error)
      toast.error(`${config.field === 'statement_received' ? '' : ''}     .`)
    }
  }, [config, canPerformAction, purchaseId, onUpdate, onOptimisticUpdate, supabase])

  const isCompleted = useCallback((item: any) => {
    if (config.field === 'statement_received') {
      return item.is_statement_received
    } else if (config.field === 'actual_received') {
      return item.is_received // actual_received_date  is_received  
    }
    return false
  }, [config.field])

  const getCompletedDate = useCallback((item: any) => {
    if (config.field === 'statement_received') {
      return item.statement_received_date
    } else if (config.field === 'actual_received') {
      return item.actual_received_date
    }
    return null
  }, [config.field])

  const getCompletedByName = useCallback((item: any) => {
    if (config.field === 'statement_received') {
      return item.statement_received_by_name
    } else if (config.field === 'actual_received') {
      //     
      return null
    }
    return null
  }, [config.field])

  return {
    config,
    handleConfirm,
    handleCancel,
    isCompleted,
    getCompletedDate,
    getCompletedByName
  }
}